import React from "react";

export class Skill extends React.Component<{image:string, title:string}, {}> {
    render() {
        return (
            <div className="skill">
                <div className="header">
                    <img src={this.props.image} alt="" />
                    <h3 className="title">{this.props.title}</h3>
                </div>
                <p className="text">
                    {this.props.children}
                </p>
            </div>
        );
    }
}

export class SkillList extends React.Component {
    render() {
        return (
            <div className="skills" id="skillset">
                <h1 className="title">
                    Extensive knowledge and skills
                </h1>
                <div className="skill-list">
                    {this.props.children}
                </div>
            </div>
        );
    }
}