import React from "react";
import Navigation from './Navigation';

import about from '../img/about.svg';

export default class Header extends React.Component<{title:String, btnText:String, btnHref:string}, {}> {
    render() {
        return (
            <header id="header">
                <Navigation>
                    <a href="#header" className="menu-item">Home</a>
                    <a href="#portfolio" className="menu-item">Portfolio</a>
                    <a href="#skillset" className="menu-item">Skills</a>
                    <a href="#education" className="menu-item">Education</a>
                    <a href="#contact" className="menu-item">Contact</a>
                </Navigation>

                <div className="content">
                    <img src={about} alt="" className="about"/>
                    <div className="about">
                        <h1 className="title">
                            {this.props.title}
                        </h1>
                        <p className="text">
                            {this.props.children}
                        </p>
                        <div className="cta-button">
                            <a href={this.props.btnHref} className="cta-link">{this.props.btnText}</a>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}