import React from "react";

import education from '../img/education.svg';
import sigla from '../img/sigla.svg';


interface StudyUniversity {
    university:string, 
    grade:string
}

export class Contact extends React.Component<{href:string, img:string, alt:string}, {}> {
    render() {
        return (
            <a href={this.props.href} className="contact-lnk">
                <img src={this.props.img} alt={this.props.alt} />
                <span className="text">{this.props.children}</span>
            </a>
        );
    }
}

export class ContactSection extends React.Component {
    render() {
        return (
            <div className="contact" id="contact">
                <h3 className="title">Reach me out</h3>
                <div className="row">
                    {this.props.children}
                </div>
            </div>
        );
    }
}

export class Education extends React.Component<{title:string, study:StudyUniversity, contacts:React.ReactElement},{}> {
    render() {
        return (
            <div className="education" id="education">
                <h3 className="title">{this.props.title}</h3>
                <div className="university">
                    <img src={education} alt="Education"/>
                    <div className="content">
                        <div className="header">
                            <img src={sigla} alt="Sigla" className="sigla"/>
                            <div className="column">
                                <h2 className="title">{this.props.study.university}</h2>
                                <h3 className="subtitle">{this.props.study.grade}</h3>
                            </div>
                        </div>
                        <p className="studies">
                            {this.props.children}
                        </p>
                    </div>
                </div>
                {this.props.contacts}
            </div>
        );
    }
}